import React from 'react'

import Header from './Header'
import SelectedShop from '../containers/SelectedShop'
import SelectedCoupon from '../containers/SelectedCoupon'
import SelectedContent from '../containers/SelectedContent'


class App extends React.Component {
	render() {
		return (
			<div>
				<Header />
				<br />
				<SelectedShop />
				<br />
				<SelectedCoupon />
				<br />
				<SelectedContent />
			</div>
		)
	}
}

export default App